@import '../../global';

$adwave-text-color: #2F2E47;

.projects {
    width: 90vw;
    margin: 10vh auto;
    display: flex;
    flex-direction: column;
    position: relative;

    h2 {
        z-index: 1;
    }
}

.project,
.project-reverse {
    width: 90vw;
    margin: 100px auto 0 auto;
    display: flex;
}

.adwave-project,
.andfm-project,
.project-card {
    position: relative;
}

.project-card {
    width: 100%;
    flex-grow: 1.5;
    border-radius: 10px;
}

.o-hidden {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
}

.insights {
    width: calc(50% + 1vw);
    max-height: 60vh;
    position: absolute;
    left: 0;
    bottom: 0;
    transform: rotateZ(-10deg) translate(-20%, 25%);
}

.public {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(50% + 1vw);
    max-height: 70vh;
    transform: translate(40%, -30%);
}

.title-row {
    position: absolute;
    display: flex;
    height: 15%;
    margin-left: 10px;
    margin-top: 10px;
    z-index: 1;
}

.description-card {
    margin: 0 20px;
    flex-shrink: 1.5;
    max-height: 100%;

    h4 {
        margin: 20px 0 10px 0;
    }

    ul {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;

        li {
            height: 10%;

            div {
                flex-direction: column;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 10px 15px 0 0;
            }

            p {

                margin-top: 10px;
                font-size: 50%;
            }
        }

        a {
            flex-direction: column;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        svg {
            height: calc(2rem + 1vh);
            width: calc(2rem + 1vw);
        }
    }
}

.adwave-project {
    svg {
        fill: #FFAF00;
    }

    .adwave-svg {
        height: 100%;
    }

    .project-image {
        bottom: -10%;
        right: 10%;
        position: absolute;
        height: 100%;
        margin-right: 5vw;

        .phone {
            position: absolute;
            height: 100%;
            transform: translate(-80%, 0);
        }

        .est {
            position: absolute;
            height: 32%;
            transform: translate(-95%, 140%);
        }

        .app {
            position: absolute;
            height: 18%;
            transform: translate(-140%, 110%);
        }
    }

    .adwave-card {
        background: linear-gradient(#FEDD26, #FEC626);
        height: 100%;

        h2 {
            color: $adwave-text-color  !important;
        }
    }
}

.andfm-project {

    .project-card {
        background: #3453f3;
    }

    .bg {
        width: 120%;
        position: absolute;
        top: 0;
    }

    .project-img {
        bottom: 0;
        right: 40%;
        position: absolute;
        height: 90%;
        z-index: 1;

        .light {
            background: transparent;
            position: absolute;
            height: 90%;
            transform: translate(-60%, 5%) rotate(-10deg);
        }

        .dark {
            background: transparent;
            position: absolute;
            height: 100%;
            transform: translate(15%, 0) rotate(15deg);
        }
    }

    .andfm-card {
        h2 {
            font-style: italic;
            font-weight: 600;
            color: white;
        }
    }

    .andfm-svg {
        background-image: url('../../assets/andfm/Logo-website.svg');
        background-repeat: no-repeat;
        background-size: contain;
        width: 5vw;
        height: 120%;
    }
}

@media screen and (max-width: 1200px) {

    .adwave-project,
    .andfm-project,
    .project-card {
        width: 100%;
        position: relative;
    }

    .insights,
    .public {
        width: calc(40% + 1vw);
    }


    .title-row {
        h2 {
            font-size: calc(2rem + 1vw);
        }
    }
}

@media screen and (max-width: 650px) {
    .project {
        flex-direction: column-reverse;
    }

    .project-reverse {
        flex-direction: column;
    }

    .project,
    .project-reverse {
        margin: 50px auto 0 auto;
    }

    .adwave-project,
    .project-card,
    .andfm-project {
        height: 700px;
    }


    .public,
    .insights {
        width: calc(30% + 1vw);
    }

    .description-card {
        text-align: center;

        ul {
            justify-content: center;
        }
    }

    .andfm-project {
        .andfm-svg {
            width: 10vw;
            height: 150%;
        }
    }
}